
.set-up-ride {
  &__content {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  &__title {
    font-size: $title-font-size-desktop;
    margin-bottom: 16px;
  }
  &__button {
    @include big-button();
  }

  &__create-your-own {
    margin-bottom: 32px;
    width: 320px;
    margin-left: auto;
    margin-right: auto;
    &--button {
      @include big-button();
    }
  }
}

@media screen and (max-width: 640px) {
  .set-up-ride {
    &__title {
      font-size: $title-font-size-phone;
    }
    
  }
}

.file-upload {
  position: absolute;
  left: -5000px;
}